body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
 background-color: black;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.magic-orb {
  position: absolute;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: radial-gradient(circle, #4cc9f0, #1d1d1d);
  box-shadow: 0 0 12px #4cc9f0, 0 0 24px #4cc9f0;
  animation: bounceAround 6s ease-in-out infinite;
    top: 20%;
  left: 20%;
  z-index: 2;
}

@keyframes bounceAround {
  0%   { top: 10%; left: 10%; }
  25%  { top: 20%; left: 80%; }
  50%  { top: 60%; left: 60%; }
  75%  { top: 80%; left: 20%; }
  100% { top: 10%; left: 10%; }
}

.glowing-spellbook {
  box-shadow: 0 0 20px 5px rgba(76, 201, 240, 0.5);
  animation: flicker 3s infinite ease-in-out;
}

@keyframes flicker {
  0%   { box-shadow: 0 0 20px 5px rgba(76, 201, 240, 0.5); }
  50%  { box-shadow: 0 0 25px 8px rgba(76, 201, 240, 0.8); }
  100% { box-shadow: 0 0 20px 5px rgba(76, 201, 240, 0.5); }
}